export async function fetchTodo({ commit }, id){
    commit('setTodo', 
    {
        id: 1,
        text: "asdf",
        categoria: "todo.categoria",
        numPaginas: "todo.numPaginas",
        ISBN: "todo.ISBN",
        autor: "todo.autor",
        editorial: "todo.editorial",
    });
    await Vue.axios.get('/libros/'+id).then( ({ data }) => {
        commit('setTodo', data);
    }).catch(err => {
        commit('todosError', err.message);
    }).finally(() => {
        console.log("Petición de fetchTodos resuelta");
    });
}

export async function addTodos({ commit, dispatch }, todo){
    await Vue.axios.post('/libros', {
        id: Date.now(),
        text: todo.text,
        categoria: todo.categoria,
        numPaginas: todo.numPaginas,
        ISBN: todo.ISBN,
        autor: todo.autor,
        editorial: todo.editorial,
    }).catch( err => {
        commit('todosError', err.message);
    }).finally(() => {
        dispatch('fetchTodos');
        console.log("Petición de addTodos resuelta");
    });
}

export async function updateTodo({ commit }, todo){
    await Vue.axios.put(`/libros/${ todo.id }`, {
        id: todo.id,
        text: todo.text,
        categoria: todo.categoria,
        numPaginas: todo.numPaginas,
        ISBN: todo.ISBN,
        autor: todo.autor,
        editorial: todo.editorial,
    }).catch( err => {
        commit('todosError', err.message);
    }).finally(() => {
        console.log("Petición de updateTodo resuelta");
    });
}

export async function updateStatusTodo({ commit, dispatch }, todo){
    await Vue.axios.put(`/libros/${ todo.id }`, {
        id: todo.id,
        text: todo.text,
        categoria: todo.categoria,
        numPaginas: todo.numPaginas,
        ISBN: todo.ISBN,
        autor: todo.autor,
        editorial: todo.editorial,
    }).catch( err => {
        commit('todosError', err.message);
    }).finally(() => {
        dispatch('fetchTodos');
        console.log("Petición de updateStatusTodo resuelta");
    });
}

export async function deleteTodo({ commit, dispatch }, todo){
    await Vue.axios.delete(`/libros/${ todo.id }`)
                    .catch( err => {
                        commit('todosError', err.message);
                    }).finally(() => {
                        dispatch('fetchTodos');
                        console.log("Petición de deleteTodo resuelta");
                    });
}